/*head*/

.head-frame {
  margin-top: 2em;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.bg-circle {
  background-color: rgba(0, 0, 0, 0.5);
  border-radius: 50%;
  width: 300px;
  height: 300px;
  display: flex;
  flex-direction: column;
  align-items: center;
  box-shadow: 0 6px 20px rgba(0, 0, 0, 0.5);
}

.bg-circle:hover {
  box-shadow: 0 6px 20px rgb(0, 0, 0);
  transition: 0.5s;
}

