@import url('https://fonts.googleapis.com/css2?family=Prompt:wght@500&display=swap');

.add-promotion {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin-top: 40px;
}

.inptt-add-promotion {
    margin-top: 40px;
    border: 2px solid rgb(159, 159, 159);
    border-radius: 4px;
}

/* modal เพิ่มโปรโมชั่น*/

.h2h2h2addmypromotionshop {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin-top: 20px;
    border-bottom: 2px solid rgb(185, 185, 185);
}

.add-addmypromotionshoppp {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.upload-promotion {
    margin-top: 10px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: left;
}

.input-add-addmypromotionshop {
    width: 100%;
    padding: 10px 0;
    margin: 5px 0;
    border-left: 0;
    border-top: 0;
    border-right: 0;
    border-bottom: 1px solid gray;
    outline: none;
    background: transparent;
    color: rgb(0, 0, 0);
}

.label-add-addmypromotionshop {
    margin-bottom: 10px;
    margin-top: 10px;
}

.button-input-add-addmypromotionshopp {
    border: 5em;
    cursor: pointer;
    outline: none;
    font-size: 16px;
    -webkit-transform: translate(0);
    transform: translate(0);
    background-image: linear-gradient(45deg, #4568dc, #b06ab3);
    padding: 0.7em 2em;
    border-radius: 1px;
    box-shadow: 1px 1px 10px rgba(255, 255, 255, 0.438);
    -webkit-transition: box-shadow 0.25s;
    transition: box-shadow 0.25s;
    color: white;
}

/*หัว modal*/
.button-top-addpromotion1 {
    font-family: 'Prompt', sans-serif;
    font-weight: bold;
    font-size: 1em;
    letter-spacing: 0.1em;
    text-decoration: none;
    color: #ffffff;
    background-color: rgb(73, 73, 255);
    display: inline-block;
    padding: 20px 20px 20px 20px;
    position: relative;
    border: none;
    border-radius: 20px;
    margin-top: 20px;
}

.button-top-addpromotion {
    font-family: 'Prompt', sans-serif;
    font-weight: bold;
    font-size: 1em;
    letter-spacing: 0.1em;
    text-decoration: none;
    color: #ffffff;
    background-color: rgb(73, 73, 255);
    display: inline-block;
    padding: 20px 20px 20px 20px;
    position: relative;
    border: none;
    border-radius: 20px;
    margin-top: 20px;
    margin-left: 10px;
}


