.h2h2h2memberprofile {
  font-size: 1.5rem;
  color: rgb(0, 0, 0);
  letter-spacing: 1px;
  text-transform: uppercase;
  font-weight: 600;
  margin-top: 40px;
  padding: 5px 30px;
  border-bottom: 2px solid rgba(0, 0, 0, 0.432);
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  font-family: 'Prompt', sans-serif;
}

.in-buttonmemberprofile {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.inputmemberprofile {
  margin-top: 40px;
  border: 2px solid rgb(155, 155, 155);
  border-radius: 10px;
  width: 50%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  font-family: 'Prompt', sans-serif;
}

.buttonmemberprofile {
  margin-left: 10px;
  display: inline-block;
  padding: 0.5em 1.7em;
  margin: 0 0.1em 0.1em 0;
  border: 0.16em solid rgba(255, 255, 255, 0);
  background-color: rgb(80, 10, 172);
  border-radius: 2em;
  box-sizing: border-box;
  text-decoration: none;
  font-family: 'Prompt', sans-serif;
  font-weight: 300;
  color: #ffffff;
  text-shadow: 0 0.04em 0.04em rgba(0, 0, 0, 0.35);
  text-align: center;
  transition: all 0.2s;
  box-shadow: 2px 2px 30px rgba(0, 32, 129, 0.534);
  
}

.buttonmemberprofile:hover {
  border-color: rgb(255, 255, 255);
}

.inf-content {
  border: 1px solid #DDDDDD;
  -webkit-border-radius: 10px;
  -moz-border-radius: 10px;
  border-radius: 10px;
  background: #ebebeb;
  box-shadow: 5px 5px 5px rgba(0, 0, 0, 0.3);
  margin-top: 20px;
  display: flex;
  flex-direction: column;
}

.img-circle {
  border-radius: 50%;
  width: 600px;
  border-width: 5px;
}

.col-md-6 {
  margin-top: 7%;
  margin-bottom: 7%;
}

.fa-pen {
  cursor: pointer;
  margin-left: 10px;
}