/*แต่ง modal*/


.h2h2h2shopprofile {
    font-size: 1.5rem;
    color: rgb(0, 0, 0);
    letter-spacing: 1px;
    text-transform: uppercase;
    font-weight: 600;
    margin-top: 40px;
    padding: 5px 30px;
    border-bottom: 2px solid rgb(0, 0, 0);
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.in-buttonshopprofile {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.inputshopprofile {
    margin-top: 40px;
    border: 2px solid rgb(155, 155, 155);
    border-radius: 10px;
    width: 50%;
}

.buttonshopprofile {
    margin-left: 10px;
    display: inline-block;
    padding: 0.5em 1.7em;
    margin: 0 0.1em 0.1em 0;
    border: 0.16em solid rgba(255, 255, 255, 0);
    background-color: rgb(80, 10, 172);
    border-radius: 2em;
    box-sizing: border-box;
    text-decoration: none;
    font-family: "Roboto", sans-serif;
    font-weight: 300;
    color: #ffffff;
    text-shadow: 0 0.04em 0.04em rgba(0, 0, 0, 0.35);
    text-align: center;
    transition: all 0.2s;
    box-shadow: 2px 2px 30px rgba(0, 32, 129, 0.534);
}
.buttonshopprofile:hover{
    border-color: rgb(255, 255, 255);
}

/*แต่งภาพ*/
.isTooltip{
    width: 100%;
    margin-top: 10px;
    margin-left: 1px;
}