@import url('https://fonts.googleapis.com/css2?family=Prompt:wght@500&display=swap');

.login-page-con {
    height: 100%;
    width: 100%;
    background-position: center;
    background-size: cover;
    position: absolute;
}

.login-page {
    height: 100%;
    width: 100%;
}

.buttonblack {
    margin-left: 40px;
}

.bl {
    text-decoration: none;
    color: #fff;
    font-family: 'Prompt', sans-serif;
}

.signin-container {
    width: 320px;
    height: 410px;
    position: relative;
    margin: 6% auto;
    background: rgba(3, 3, 3, 0.658);
    box-shadow: 0 6px 20px rgba(3, 3, 3, 0.658);
    padding: 5px;
    overflow: hidden;
    border-radius: 50px;
}

.fa-sign-in-alt{
    color: rgb(255, 255, 255);
}

.title-login {
    color: rgb(209, 209, 209);
    border-bottom: 2px solid rgb(3, 3, 3);
    padding: 5px 10px;
    margin-bottom: 10px;
    font-family: 'Prompt', sans-serif;
}

.form-signin {
    margin-top: 20px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    font-family: 'Prompt', sans-serif;
}

.form2-signin {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.form-signin-email {
    margin: 5px 15px;
    color: rgb(209, 209, 209);
    font-family: 'Prompt', sans-serif;
}

.form-signin-password {
    margin: 5px 15px;
    color: rgb(209, 209, 209);
    font-family: 'Prompt', sans-serif;
}

.input-email-login {
    width: 90%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    border-radius: 10px;
    font-family: 'Prompt', sans-serif;
}

.input-password-login {
    width: 90%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    border-radius: 10px;
    font-family: 'Prompt', sans-serif;
}

.bn13 {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    font-family: 'Prompt', sans-serif;
    
}

.div-button-login-n {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.p-loginstatus{
    margin-top: 10px;
    font-family: 'Prompt', sans-serif;
}

.button-login-n {
    margin-top: 10px;
    border: 5em;
    cursor: pointer;
    outline: none;
    font-size: 16px;
    -webkit-transform: translate(0);
    transform: translate(0);
    background-image: linear-gradient(45deg, #4568dc, #b06ab3);
    padding: 0.8em 2em;
    border-radius: 1px;
    box-shadow: 1px 1px 10px rgba(255, 255, 255, 0.438);
    -webkit-transition: box-shadow 0.25s;
    transition: box-shadow 0.25s;
    color: white;
    font-family: 'Prompt', sans-serif;
}