@import url('https://fonts.googleapis.com/css2?family=Prompt:wght@500&display=swap');

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  /*font-family: "PT Sans", sans-serif;*/
}

.homeimg {
  object-fit: cover;
  width: 100%;
  height: 100%;
  position: fixed;
  z-index: -1;
}

.hero-container {
  height: 100vh;
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  box-shadow: inset 0 0 0 1000px rgba(0, 0, 0, 0.2);
  object-fit: contain;
}

.hero-container > h1 {
  color: #fff;
  font-size: 100px;
  margin-top: -100px;
}

.hero-container > p {
  margin-top: 8px;
  color: #fff;
  font-size: 32px;
 /* font-family: "Trebuchet MS", "Lucida Sans Unicode", "Lucida Grande",
    "Lucida Sans", Arial, sans-serif;*/
}

.hero-btns {
  margin-top: 32px;
}

.hero-btns .btn {
  margin: 6px;
  font-family: 'Prompt', sans-serif;
}

.p-hero-container{
  /*font-family: 'Prompt', sans-serif;*/
}

@media screen and (max-width: 1024px) {
  .hero-container > h1 {
    font-size: 50px;
    margin-top: -150px;
  }
}

@media screen and (max-width: 768px) {
  .hero-container > h1 {
    font-size: 50px;
    margin-top: -100px;
  }

  .hero-container > p {
    font-size: 16px;
  }

  .btn-mobile {
    display: block;
    text-decoration: none;
  }

  .btn {
    width: 100%;
    opacity: 0.2px;
  }
}

.fa-angle-right{
  margin-left: 30px;
}

.fa-angle-double-down{
  margin-left: 10px;
}

@media screen and (max-width: 650px) {
  .hero-container > h1 {
    font-size: 30px;
    margin-top: -100px;
  }

  .hero-container > p {
    font-size: 14px;
  }

  .btn-mobile {
    display: block;
