    font-size: 25px;
    margin-top: -100px;
  }

  .hero-container > p {
    font-size: 12px;
  }

  .btn-mobile {
    display: block;
    text-decoration: none;
  }

  .btn {
    width: 100%;
    opacity: 0.2px;
  }
}

@media screen and (max-width: 290px) {
