@import url('https://fonts.googleapis.com/css2?family=Prompt:wght@500&display=swap');

.register-con {
    width: 320px;
    height: 500px;
    position: relative;
    margin: 6% auto;
    background: rgba(3, 3, 3, 0.658);
    box-shadow: 0 6px 20px rgba(3, 3, 3, 0.658);
    padding: 5px;
    overflow: hidden;
    border-radius: 50px;
}

.button-register {
    width: 220px;
    margin: 35px auto;
    position: relative;
    box-shadow: 0 0 20px 9px #ff61241f;
    border-radius: 30px;
}

.button-member {
    padding: 10px 30px;
    cursor: pointer;
    background: transparent;
    border: 0;
    outline: none;
    position: relative;
    color: #fff;
    font-family: 'Prompt', sans-serif;
}

.button-shop {
    padding: 10px 30px;
    cursor: pointer;
    background: transparent;
    border: 0;
    outline: none;
    position: relative;
    color: #fff;
    font-family: 'Prompt', sans-serif;
}

#buttonregis {
    top: 0;
    left: 0;
    position: absolute;
    width: 110px;
    height: 100%;
    background: linear-gradient(to right, #cf10ff, #2001ac);
    border-radius: 30px;
    transition: .5s;
}

.form-register {
    top: 180px;
    position: absolute;
    width: 280px;
    transition: 0.5s;
}

.member-register {
    margin-top: -50px;
}

.member-register-name {
    width: 90%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin-top: 10px;
    border-radius: 10px;
    font-family: 'Prompt', sans-serif;
}

.submit-member {
    width: 85%;
    padding: 10px 30px;
    cursor: pointer;
    display: block;
    background: linear-gradient(to right, #cf10ff, #2001ac);
    border: 0;
    outline: none;
    border-radius: 30px;
    margin-top: 50px;
    margin-left: 10px;
    color: #fff;
    font-family: 'Prompt', sans-serif;
}

.p-member-register-name{ 
    margin-left: 40px;
    font-family: 'Prompt', sans-serif;
    margin-top: 10px;
   
}

.shop-register {
    margin-top: -60px;
}

.shop-register-name {
    width: 90%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    border-radius: 10px;
    font-family: 'Prompt', sans-serif;
    margin-top: 10px;
}

.submit-shop {
    width: 85%;
    padding: 10px 30px;
    cursor: pointer;
    display: block;
    background: linear-gradient(to right, #cf10ff, #2001ac);
    border: 0;
    outline: none;
    border-radius: 30px;
    margin-top: 30px;
    margin-left: 10px;
    color: rgb(255, 255, 255);
    font-family: 'Prompt', sans-serif;
}

.p-shop-register-name{
    margin-top: 10px;
    margin-left: 40px;
    font-family: 'Prompt', sans-serif;
}

.labeltype {
    margin-right: 15px;
    color: gray;
    font-size: 16px;
    font-family: 'Prompt', sans-serif;
}

.menutypeshop {
    border-radius: 10px;
    padding: 6px 0;
    font-family: 'Prompt', sans-serif;
    margin-top: 10px;
}

.option-register{
    font-family: 'Prompt', sans-serif;
}

#memberregisterr {
    left: 35px;
}

#shopregisterr {
    left: 380px;
}
