.transferpointshop {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.fa-times{
  float: right;
  color: rgb(179, 40, 40);
}

.div-transferpointshop {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.h2transferpointshop {
  font-size: 1.5rem;
  color: rgb(0, 0, 0);
  letter-spacing: 1px;
  text-transform: uppercase;
  font-weight: 600;
  margin-top: 25px;
  padding: 5px 30px;
  border-bottom: 2px solid rgb(0, 0, 0);
  font-family: 'Prompt', sans-serif;

}

.fa-check {
  margin-top: 5px;
  color: rgb(1, 255, 128);
}

.h3transfershop {
  font-size: 1.5rem;
  color: rgb(0, 0, 0);
  letter-spacing: 1px;
  text-transform: uppercase;
  font-weight: 600;
  margin-top: 40px;
  padding: 5px 30px;
  border-bottom: 2px solid rgb(159, 159, 159);
  border-top: 2px solid rgb(159, 159, 159);
  font-family: 'Prompt', sans-serif;
}

.buttontransfershop {
  width: 160px;
  font-size: 16px;
  font-weight: 600;
  color: #fff;
  cursor: pointer;
  margin: 10px 180px;
  height: 45px;
  text-align: center;
  border: none;
  background-size: 300% 100%;
  border-radius: 50px;
  moz-transition: all .4s ease-in-out;
  -o-transition: all .4s ease-in-out;
  -webkit-transition: all .4s ease-in-out;
  transition: all .4s ease-in-out;
  background-image: linear-gradient( to right, #ff00dd, #8e06fd, #5900ff, #1900ff);
  box-shadow: 0 4px 15px 0 #a200ff8a;
}

.buttontransfershop:hover {
  background-position: 100% 0;
  moz-transition: all .4s ease-in-out;
  -o-transition: all .4s ease-in-out;
  -webkit-transition: all .4s ease-in-out;
  transition: all .4s ease-in-out;
}

.buttontransfershop:focus {
  outline: none;
}
