@import url('https://fonts.googleapis.com/css2?family=Prompt:wght@500&display=swap');
.addpromotion-page {
    height: 100%;
    width: 100%;
    background-position: center;
    background-size: cover;
    position: absolute;
}

.addpromotion-page-con {
    width: 380px;
    height: 530px;
    position: relative;
    margin: 6% auto;
    background: rgba(3, 3, 3, 0.658);
    box-shadow: 0 6px 20px rgba(3, 3, 3, 0.658);
    padding: 5px;
    overflow: hidden;
    border-radius: 50px;
}

.button-addpromotion-page-con {
    width: 100%;
    margin: 35px 0;
    position: relative;
    box-shadow: 0 0 20px 9px #ff61241f;
    border-radius: 30px;
}

.button-add-promotion-point {
    padding: 10px 25px;
    cursor: pointer;
    background: transparent;
    border: 0;
    outline: none;
    position: relative;
    color: #fff;
    font-size: 14px;
}

.button-add-promotion-product {
    padding: 10px 5px;
    cursor: pointer;
    background: transparent;
    border: 0;
    outline: none;
    position: relative;
    color: #fff;
    font-size: 14px;
}

#buttonaddpromotionpage {
    top: 0;
    left: 0;
    position: absolute;
    margin-left: 10px;
    /*padding: 10px 75px;*/
    width: 47%;
    height: 100%;
    background: linear-gradient(to right, #cf10ff, #2001ac);
    border-radius: 30px;
    transition: .5s;
    font-family: 'Mitr', sans-serif;
}

.form-addpromotionpoints {
    top: 180px;
    position: absolute;
    width: 280px;
    transition: 0.5s;
}

.div-form-addpromotionpoints {
    margin-top: -80px;
}

.div-form-addpromotionproducts {
    margin-top: -80px;
}

.addpromotionpoints-shop-name {
    width: 100%;
    padding: 10px 0;
    margin: 5px 0;
    border-left: 0;
    border-top: 0;
    border-right: 0;
    border-bottom: 1px solid gray;
    outline: none;
    background: transparent;
    color: #fff;
}

.addpromotionproducts-shop-name {
    width: 100%;
    padding: 10px 0;
    margin: 5px 0;
    border-left: 0;
    border-top: 0;
    border-right: 0;
    border-bottom: 1px solid gray;
    outline: none;
    background: transparent;
    color: #fff;
}

.submit-addpromotionpoints {
    width: 85%;
    padding: 10px 30px;
    cursor: pointer;
    display: block;
    margin: auto;
    background: linear-gradient(to right, #cf10ff, #2001ac);
    border: 0;
    outline: none;
    border-radius: 30px;
    margin-top: 40px;
    color: #fff;
}

.form-addpromotionproducts {
    top: 180px;
    position: absolute;
    width: 280px;
    transition: 0.5s;
}

.div-form-addpromotionproducts {
    margin-top: -60px;
}

.addpromotionproducts-product-name {
    width: 100%;
    padding: 10px 0;
    margin: 5px 0;
    border-left: 0;
    border-top: 0;
    border-right: 0;
    border-bottom: 1px solid gray;
    outline: none;
    background: transparent;
    color: #fff;
}

.submit-addpromotionproducts {
    width: 85%;
    padding: 10px 30px;
    cursor: pointer;
    display: block;
    margin: auto;
    background: linear-gradient(to right, #cf10ff, #2001ac);
    border: 0;
    outline: none;
    border-radius: 30px;
    margin-top: 20px;
    color: rgb(255, 255, 255);
}

#addpromotionpoints {
    left: 50px;
}

#addpromotionproducts {
    left: 450px;
}

.input-label-category-food {
    width: 15px;
    height: 15px;
    padding: 10px 0;
    margin: 5px -128px;
    outline: none;
    background: transparent;
    color: #fff;
}

.input-label-category-drink {
    width: 15px;
    height: 15px;
    padding: 10px 0;
    margin: 5px -128px;
    outline: none;
    background: transparent;
    color: #fff;
}

.label-category {
    margin-top: 10px;
