}


.feature__item__link {
  display: flex;
  flex-flow: column;
  width: 100%;
  box-shadow: 0 6px 20px rgba(56, 125, 255, 0.17);
  -webkit-filter: drop-shadow(0 6px 20px rgba(56, 125, 255, 0.017));
  filter: drop-shadow(0 6px 20px rgba(56, 125, 255, 0.017));
  border-radius: 10px;
  overflow: hidden;
  text-decoration: none;
  font-family: 'Prompt', sans-serif;
  justify-content: center;
  align-items: center;
  text-align: center;
}

.feature__item__pic-wrap {
  position: relative;
  width: 100%;
  padding-top: 67%;
  overflow: hidden;
}

.fade-img {
  -webkit-animation-name: fade-img;
          animation-name: fade-img;
  -webkit-animation-duration: 2s;
          animation-duration: 2s;
}


.feature__item__pic-wrap::after {
  content: attr(data-category);
  position: absolute;
  bottom: 0;
  margin: 0px -50px;
  padding: 6px 8px;
  max-width: calc((100%) - 60px);
  font-size: 12px;
  font-weight: 700;
  color: #fff;
  background-color: #1f98f4;
  box-sizing: border-box;
  font-family: 'Prompt', sans-serif;

}

.feature__item__img {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  display: block;
  width: 100%;
  max-width: 100%;
  height: 100%;
  max-height: 100%;
  object-fit: cover;
  transition: all 0.2s linear;
  
}

.feature__item__img:hover {
  -webkit-transform: scale(1.1);
          transform: scale(1.1);

}



.feature__item__info {
  padding: 20px 30px 30px;
}

.feature__item__text {
  color: #252e48;
  font-size: 18px;
  line-height: 24px;
  font-family: 'Prompt', sans-serif;
