@import url('https://fonts.googleapis.com/css2?family=Prompt:wght@500&display=swap');
p {
  font-family: 'Prompt', sans-serif;
}

.fa-exchange-alt {
  margin-right: 10px;
}

.fa-redo-alt {
  margin-right: 10px;
}

.fa-tag {
  margin-right: 10px;
}

.main-menu {
  display: inline-flex;
  align-items: center;
}

.fa-angle-left {
  color: rgb(255, 255, 255);
}

.bl {
  margin-left: 10px;
}

.bl:hover {
  color: rgb(255, 255, 255);
}

/*Table*/

.table-dark {
  display: inline-table;
  flex-direction: column;
  align-items: center;
  height: 100vh;
  color: rgb(153, 153, 153);
  font-family: 'Prompt', sans-serif;
}

.deposit, .deposit-detail, .withdraw, .withdraw-detail {
  margin-top: 10px;
  margin-left: 50px;
}

.amountminus, .amountplus {
  margin-top: 10px;
}

/*-----------------font-----------------*/

.deposit {
  font-size: 20px;
  color: rgb(113, 194, 113);
  font-family: 'Prompt', sans-serif;
}

.withdraw {
  font-size: 20px;
  color: rgb(185, 77, 77);
  font-family: 'Prompt', sans-serif;
}

.amountplus {
  font-size: 20px;
  color: rgb(113, 194, 113);
  font-family: 'Prompt', sans-serif;
}

.amountminus {
  font-size: 20px;
  color: rgb(185, 77, 77);
  font-family: 'Prompt', sans-serif;
}

.time {
  font-size: 15px;
  font-family: 'Prompt', sans-serif;
}

.deposit-detail {
  font-size: 15px;
  font-family: 'Prompt', sans-serif;
}

.withdraw-detail {
  font-size: 15px;
  font-family: 'Prompt', sans-serif;
}