@import url('https://fonts.googleapis.com/css2?family=Prompt:wght@500&display=swap');
/*modal แต่งไว้หน้า promotion*/

/*แต่ง modal รูปภาพ*/

.container-promotionshop {
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
  margin-top: 20px;
}

.shopbox-promotinshop {
  display: flex;
  flex-grow: 0.5;
  flex-direction: column;
  align-items: center;
  border-radius: 10px;
  margin: 20px;
  width: 250px;
  height: 210px;
  cursor: pointer;
  position: relative;
  border: 2px solid rgb(155, 155, 155);
  border-radius: 4px;
}

.shopbox-promotinshop:hover {
  box-shadow: 2px 2px 30px rgb(155, 155, 155);
  transition: .5s;
}

/*แต่งปุ่มร้านค้าของฉัน*/

.fa-store {
  margin-right: 10px;
}


.shopbox-promotinnnnn {
  display: flex;
  justify-content: center;
  align-items: center;
  
}

.button-cotainerpromotionnnn {
  font-family: 'Prompt', sans-serif;
  font-weight: bold;
  font-size: 1em;
  letter-spacing: 0.1em;
  text-decoration: none;
  color: #ffffff;
  background-color: rgb(73, 73, 255);
  display: inline-block;
  padding: 20px 30px 20px 30px;
  position: relative;
  border: none;
  border-radius: 20px;
  margin-top: 20px;
  
}

/*แต่งบล็อกร้านค้า*/

.fa-shopify {
  margin-top: 20px;
}

/*แต่งภาพ */

.shopbox-img {
  /*width: 150px;
  height: 160px;*/
  margin: 20px;
  cursor: pointer;
}

.img-fluid-shopbox {
  width: 100%;
  height: 100%;
  object-fit: contain;
  object-position: center;
}

.img-fluid-shopbox:hover {
  transform: scale(1.1);
}
