/*แต่งหมวดหมู่*/

@import url('https://fonts.googleapis.com/css2?family=Prompt:wght@500&display=swap');
.addstock-page-con {
    height: 100%;
    width: 100%;
    background-position: center;
    background-size: cover;
    position: absolute;
}

.addstock-container {
    width: 320px;
    height: 550px;
    position: relative;
    margin: 6% auto;
    background: rgba(3, 3, 3, 0.658);
    box-shadow: 0 6px 20px rgba(3, 3, 3, 0.658);
    padding: 5px;
    overflow: hidden;
    border-radius: 50px;
}

.form-addstock {
    margin-top: 20px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    font-family: 'Prompt', sans-serif;
}

.title-addstock {
    color: rgb(209, 209, 209);
    border-bottom: 2px solid rgb(3, 3, 3);
    padding: 5px 10px;
    margin-bottom: 10px;
    font-family: 'Prompt', sans-serif;
}

.form-addstock-name {
    margin: 5px 15px;
    color: rgb(209, 209, 209);
    font-family: 'Prompt', sans-serif;
}

.form2-addstock {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.input-addstock-name {
    width: 90%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    border-radius: 10px;
    font-family: 'Prompt', sans-serif;
}

.input-addstock-name1 {
    width: 90%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    border-radius: 10px;
    margin-left: 15px;
    font-family: 'Prompt', sans-serif;
}

.p-input-add-stock {
    font-family: 1rem 'Prompt', sans-serif;
    margin-top: 10px;
    margin: 5px 15px;
    color: rgb(209, 209, 209);
}

.label-input-add-stock {
    color: rgb(209, 209, 209);
    font-family: 1rem 'Prompt', sans-serif;
    display: inline;
    justify-content: center;
    align-items: center;
    text-align: center;
    margin-left: -110px;
}

.label-input-add-stock:hover {
    border: none;
    outline: none;
}

.div-add-stock {
    display: inline;
    justify-content: center;
    align-items: center;
    text-align: center;
}

.input-add-stock {
    display: inline;
    justify-content: center;
    align-items: center;
    text-align: center;
    width: 15px;
    height: 15px;
}

.input-add-stock:focus {
    border: none;
    outline: none;
    box-shadow: none;
}

.p-button-addstock {
    text-align: center;
    color: rgb(255, 40, 40);
    margin-top: 5px;
}

.divbutton-addstock {
    justify-content: center;
    align-items: center;
    text-align: center;
}

.button-addstock {
    border: 0;
    text-align: center;
    display: inline-block;
    padding: 14px;
    width: 100px;
    margin: 7px;
    color: #ffffff;
    background-color: #36a2eb;
    border-radius: 10px;
    font-family: 'Prompt', sans-serif;
    font-weight: 600;
    text-decoration: none;
    transition: box-shadow 200ms ease-out;
}