@import url('https://fonts.googleapis.com/css2?family=Prompt:wght@500&display=swap');

.feature {
  padding: 4rem;
  background: #fff;
  font-family: 'Prompt', sans-serif;
}

.feature {
  text-align: center;
}

.image-feature{
  display: inline-block;
  align-items: center;
  justify-content: center;
  align-items: center;
  text-align: center;
  margin-left:50px ;
  margin-right:50px ;
  margin-bottom: 50px; 
}
.image-feature:hover{
  transform: scale(1.1); 
}

.div-img-feature-home1:hover{
  opacity: 0.5;
}

.div-img-feature-home1{
  cursor: pointer;
  align-items: center;
  width: 100%;
}

.img-feature-home1{
  width: 100px;
}


.feature__container {
  display: flex;
  flex-flow: column;
  align-items: center;
  max-width: 1120px;
  width: 90%;
  margin: 0 auto;
}

.feature__wrapper {
  position: relative;
  margin: 0px 0 40px;
}

.feature__items {
  font-family: 'Prompt', sans-serif;
}


.feature__item {
  display: flex;
  flex: 1;
  margin: 0 1rem;
  border-radius: 10px;
  font-family: 'Prompt', sans-serif;

}


.feature__item__link {
  display: flex;
  flex-flow: column;
  width: 100%;
  box-shadow: 0 6px 20px rgba(56, 125, 255, 0.17);
  -webkit-filter: drop-shadow(0 6px 20px rgba(56, 125, 255, 0.017));
  filter: drop-shadow(0 6px 20px rgba(56, 125, 255, 0.017));
  border-radius: 10px;
  overflow: hidden;
  text-decoration: none;
  font-family: 'Prompt', sans-serif;
  justify-content: center;
  align-items: center;
  text-align: center;
}

.feature__item__pic-wrap {
  position: relative;
  width: 100%;
  padding-top: 67%;
  overflow: hidden;
}

.fade-img {
  animation-name: fade-img;
  animation-duration: 2s;
}


.feature__item__pic-wrap::after {
  content: attr(data-category);
  position: absolute;
  bottom: 0;
  margin: 0px -50px;
  padding: 6px 8px;
  max-width: calc((100%) - 60px);
  font-size: 12px;
  font-weight: 700;
  color: #fff;
  background-color: #1f98f4;
  box-sizing: border-box;
  font-family: 'Prompt', sans-serif;

}

.feature__item__img {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  display: block;
  width: 100%;
  max-width: 100%;
  height: 100%;
  max-height: 100%;
  object-fit: cover;
  transition: all 0.2s linear;
  
}

.feature__item__img:hover {
  transform: scale(1.1);

}



.feature__item__info {
  padding: 20px 30px 30px;
}

.feature__item__text {
  color: #252e48;
  font-size: 18px;
  line-height: 24px;
  font-family: 'Prompt', sans-serif;
}

@media only screen and (min-width: 1200px) {
  .content__blog__container {
    width: 84%;
  }
}

@media only screen and (min-width: 1024px) {
  .feature__items {
    display: flex;
  }
}

@media only screen and (max-width: 1024px) {
  .feature__item {
    margin-bottom: 2rem;
  }
}
