@import url('https://fonts.googleapis.com/css2?family=Prompt:wght@500&display=swap');
/*แต่งงงงงงงงงงงงงงงงง*/

.top-productstck {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding: 4rem;
}

.container-productstck {
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
    margin-top: 20px;
}

.shopbox-productstck {
    display: flex;
    flex-grow: 0.5;
    flex-direction: column;
    align-items: center;
    border-radius: 10px;
    margin: 20px;
    width: 250px;
    height: 300px;
    cursor: pointer;
    position: relative;
    border: 1px solid #DDDDDD;
    -webkit-border-radius: 10px;
    -moz-border-radius: 10px;
    background: #ebebeb;
    border-radius: 10px;
    box-shadow: 2px 2px 8px rgba(0, 0, 0, 0.3);
    -webkit-filter: drop-shadow(0 6px 20px rgba(56, 125, 255, 0.017));
    filter: drop-shadow(0 6px 20px rgba(56, 125, 255, 0.017));
    overflow: hidden;
    text-decoration: none;
}

.shopbox-productstck:hover {
    box-shadow: 0 6px 20px rgba(56, 126, 255, 0.486);
    transition: .5s;
}

.boxproductstck {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 100%;
    padding: 10px 20px;
}

@media screen and (max-width: 370px) {
    .shopbox-productstck {
        display: flex;
        flex-grow: 0.5;
        flex-direction: column;
        align-items: center;
        border-radius: 10px;
        margin: 20px;
        width: 250px;
        height: 300px;
        cursor: pointer;
        position: relative;
        border: 2px solid rgb(155, 155, 155);
        border-radius: 4px;
        box-shadow: 0 6px 20px rgba(56, 125, 255, 0.17);
        -webkit-filter: drop-shadow(0 6px 20px rgba(56, 125, 255, 0.017));
        filter: drop-shadow(0 6px 20px rgba(56, 125, 255, 0.017));
        overflow: hidden;
        text-decoration: none;
    }
}

.fa-cart-plus {
    margin-right: 10px;
}

