/*Colored Content Boxes
------------------------------------*/

.servive-block {
  padding: 20px 30px;
  text-align: center;
  margin-bottom: 20px;
}

.servive-block p, .servive-block h2 {
  color: #fff;
}

.servive-block h2 a:hover {
  text-decoration: none;
}

.servive-block-light, .servive-block-default {
  background: #fafafa;
  border: solid 1px #eee;
}

.servive-block-default:hover {
  box-shadow: 0 0 8px #eee;
}

.servive-block-light p, .servive-block-light h2, .servive-block-default p, .servive-block-default h2 {
  color: #555;
}

.servive-block-yelloww {
  border: 1px solid #DDDDDD;
  -webkit-border-radius: 10px;
  -moz-border-radius: 10px;
  background: #ebebeb;
  border-radius: 20px;
  box-shadow: 2px 2px 8px  rgba(0, 0, 0, 0.3);
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.servive-block-yelloww:hover {
  box-shadow: 2px 2px 30px rgba(0, 0, 0, 0.322);
  transition: .5s;
  transform: scale(1.1);
}

.servive-block-purplee {
  border: 1px solid #DDDDDD;
  -webkit-border-radius: 10px;
  -moz-border-radius: 10px;
  background: #ebebeb;
  box-shadow: 2px 2px 8px rgb(0, 0, 0, 0.3);
  border-radius: 20px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.servive-block-purplee:hover {
  box-shadow: 2px 2px 30px rgba(0, 0, 0, 0.322);
  transition: .5s;
  transform: scale(1.1);
}

.servive-block-aquaa {
  border: 1px solid #DDDDDD;
  -webkit-border-radius: 10px;
  -moz-border-radius: 10px;
  background: #ebebeb;
  box-shadow: 2px 2px 8px rgb(0, 0, 0, 0.3);
  border-radius: 20px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.servive-block-aquaa:hover {
  box-shadow: 2px 2px 30px rgba(0, 0, 0, 0.5);
  transition: .5s;
  transform: scale(1.1);
}

/*head*/

.head-frame {
  margin-top: 2em;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.bg-circle {
  background-color: rgba(0, 0, 0, 0.5);
  border-radius: 50%;
  width: 300px;
  height: 300px;
  display: flex;
  flex-direction: column;
  align-items: center;
  box-shadow: 0 6px 20px rgba(0, 0, 0, 0.5);
  
}

.bg-circle:hover {
  box-shadow: 0 6px 20px rgb(0, 0, 0);
  transition: 0.5s;
}

.profile-button {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-bottom: 50px;
  margin-top: 30px;
}
