/*profile button*/

.bnmenumember {
  border: 1px solid #DDDDDD;
  -webkit-border-radius: 10px;
  -moz-border-radius: 10px;
  background: #ebebeb;
  box-shadow: 2px 2px 8px rgb(0, 0, 0, 0.3);
  width: 160px;
  font-size: 20px;
  font-weight: 600;
  font-family: 'Prompt', sans-serif;
  color: rgb(51, 51, 51);
  cursor: pointer;
  margin: 20px;
  height: 55px;
  text-align:center;
  border: none;
  background-size: 300% 100%;
  border-radius: 20px;
  moz-transition: all .4s ease-in-out;
  -o-transition: all .4s ease-in-out;
  -webkit-transition: all .4s ease-in-out;
  transition: all .4s ease-in-out;
  background-image: linear-gradient(
      to right,
      #7625e193,
      #2bb1ebc5,
      #9830dd,
      #9625e1
    );
}

.bnmenumember:hover{
  transform: scale(1.1);
}


@media (max-width: 768px) {
  .menumember-page {
    height: 120vh;
  }
}

/*iphone 5/5s/se*/

@media (max-height: 512px) {
  .menumember-page {
    height: 250vh;
  }
}

@media (max-height: 320px) {
  .menumember-page {
    height: 280vh;
  }
}

/*surface*/

@media (height: 720px) {
  .menushop-page {
    height: 145vh;
  }
}

/*lower iphone 6/7/8*/

@media (max-width: 375px) {
  .menumember-page {
    height: 160vh;
  }
}

.margin-bottom-10 {
  cursor: pointer;
