label {
  font-size: 15px;
  color: rgb(0, 0, 0);
  margin-bottom: 5px;
}

input {
  margin-top: 1px;
  min-width: 18em;
  height: 37px;
  padding: 0px 10px;
  font-size: 16px;
  font-family: sans-serif;
  background-color: #cacaca;
  border: 0;
  border-radius: 4px;
  margin-bottom: 31px;
  transition: all 250ms ease-in-out;
  color: #000000;
}

input:focus {
  outline: none;
  box-shadow: 0px 0px 2px 0.8px #3b3b3b;
  background-color: #9b9b9b;
}