@import url('https://fonts.googleapis.com/css2?family=Prompt:wght@500&display=swap');


/*กล่องใหญ่*/

.top-promotion {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 4rem;
}

/*ส่วนของเเต่งโปรโมชั่น input ด้านบน*/

.toppic-promotin {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  margin-top: 40px;
}

/*หัวข้อโปรโมชั้น*/

.h2promotionmember {
  font-size: 1.5rem;
  color: rgb(0, 0, 0);
  letter-spacing: 1px;
  text-transform: uppercase;
  font-weight: 600;
  margin-top: 40px;
  padding: 5px 30px;
  border-bottom: 2px solid rgb(0, 0, 0);
  font-family: 'Prompt', sans-serif;
}

/*ช่องค้นหา*/

.divinsearch-1 {
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
 
}

.insearch-divinsearch{
  justify-content: center;
  align-items: center;
  margin-top: 40px;
  border-radius: 10px;
  font-family: 'Prompt', sans-serif;
}


/*ปุ่มค้นหา*/

.fa-search {
  cursor: pointer;
  margin-top: 40px;
  margin-left: 10px;
  
}

.container-promotion {
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
  margin-top: 20px;
}

.shopbox-promotin {
  display: flex;
  flex-grow: 0.5;
  flex-direction: column;
  align-items: center;
  border-radius: 10px;
  margin: 20px;
  width: 250px;
  height: 230px;
  cursor: pointer;
  position: relative;
  border: 1px solid #DDDDDD;
  -webkit-border-radius: 10px;
  -moz-border-radius: 10px;
  background: #ebebeb;
  border-radius: 10px;
  box-shadow: 2px 2px 8px rgba(0, 0, 0, 0.3);
  -webkit-filter: drop-shadow(0 6px 20px rgba(56, 125, 255, 0.017));
  filter: drop-shadow(0 6px 20px rgba(56, 125, 255, 0.017));
  overflow: hidden;
  text-decoration: none;
}

.shopbox-promotin:hover {
  box-shadow: 0 6px 20px rgba(56, 126, 255, 0.486);
  transition: .5s;
}

.divimg-promotin {
  /*width: 100px;
  height: 110px;*/
  margin: 20px;
  cursor: pointer;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.img-promotin {
  width: 100%;
  justify-content: center;
  align-items: center;
  text-align: center;
}

.img-promotin:hover {
  transform: scale(1.1);
}


.boxpromotion {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
  padding: 10px 20px;

}

.p-promotin{
  font-family: 'Prompt', sans-serif;
}

