@import url('https://fonts.googleapis.com/css2?family=Prompt:wght@500&display=swap');

.div-img-logo2 {
  display: inline;
  margin-right: 20px;
}

.buttontop-up {
  border: 5em;
  cursor: pointer;
  outline: none;
  font-size: 16px;
  -webkit-transform: translate(0);
  transform: translate(0);
  background-image: linear-gradient(45deg, #4568dc, #4568dc);
  padding: 0.7em 2em;
  border-radius: 10px;
  box-shadow: 1px 1px 10px rgba(255, 255, 255, 0.438);
  -webkit-transition: box-shadow 0.25s;
  transition: box-shadow 0.25s;
  color: white;
  margin: 20px 10px;
  font-family: 'Prompt', sans-serif;

}

.fa-plus {
  margin-right: 10px;
}

.calculate-con {
  display: flex;
  flex-direction: column;
  float: right;
  margin-right: 1em;
}

.h2promotion {
  font-size: 40px;
  color: rgb(0, 0, 0);
  font-family: 'Prompt', sans-serif;
  border-bottom: 3px solid rgba(159, 159, 159, 159);
  padding: 5px 15px;
}

.h1promotion {
  font-size: 23px;
  color: rgb(0, 0, 0);
  font-family: 'Prompt', sans-serif;
}

.textbox-cal {
  width: 100%;
  overflow: hidden;
  font-size: 20px;
  padding: 8px 0;
  margin: 8px 0;
}

.fa-phone-square-alt {
  width: 26px;
  margin: 0px 2px;
  float: left;
  text-align: center;
  color: #000;
}

.fa-dollar-sign {
  width: 26px;
  margin: 0px 2px;
  float: left;
  text-align: center;
  color: #000;
}

.inputcalculate {
  width: 80%;
  border: none;
  outline: none;
  background: none;
  color: #ffffff;
  font-size: 18px;
  float: left;
  margin: 10px 10px;
  border: 2px solid rgba(159, 159, 159, 159);
  border-radius: 4px;
}

.button-cal {
  width: 160px;
  font-size: 16px;
  font-weight: 600;
  color: #fff;
  cursor: pointer;
  margin: 10px 90px;
  height: 45px;
  text-align: center;
  border: none;
  background-size: 300% 100%;
  border-radius: 50px;
  moz-transition: all .4s ease-in-out;
  -o-transition: all .4s ease-in-out;
  -webkit-transition: all .4s ease-in-out;
  transition: all .4s ease-in-out;
  background-image: linear-gradient( to right, #f700ff, #a304ff, #4b00fa, #1100fd);
  box-shadow: 0 4px 15px 0 rgba(174, 0, 255, 0.644);
}

.button-cal:hover {
  background-position: 100% 0;
  moz-transition: all .4s ease-in-out;
  -o-transition: all .4s ease-in-out;
  -webkit-transition: all .4s ease-in-out;
  transition: all .4s ease-in-out;
}

.button-cal:focus {
  outline: none;
}

/*product table scroll*/

.my-custom-scrollbar {
  position: relative;
  height: 500px;
  overflow: scroll;
  overflow-x: hidden;
}

.table-wrapper-scroll-y {
  display: block;
}

/*แต่งตารราง*/

.mb-0 {
  background-color: rgba(255, 255, 255, 0.5);
  border-color: rgb(128, 128, 128);
}

.th-table {
  text-align: center;
  vertical-align: middle;
  font-size: 30px;
  color: rgb(33, 31, 47);
  font-family: 'Prompt', sans-serif;
}

.td-table {
  text-align: center;
  vertical-align: middle;
  font-size: 20px;
  color: rgb(33, 31, 47);
  font-family: 'Prompt', sans-serif;
}

/*แต่งปุ่มลบรายการ*/

.button-delete-item {
  height: 40px;
  background-color: #ff0000b6;
  border: none;
  color: rgb(255, 194, 194);
  padding: 0.1em 0.1em;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 16px;
  border: 2px solid rgb(59, 59, 59);
  font-family: 'Prompt', sans-serif;
}

.fa-times-circle {
  color: rgb(255, 133, 133);
}

/*แต่ง modal*/

.divcalculate-modal {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.pcalculate-modal {
  border-bottom: 2px solid rgb(0, 0, 0);
  padding: 10px 80px;
  margin-top: 40px;
}

.buttoncalculate-modal1 {
  margin-left: 10px;
  cursor: pointer;
  outline: none;
  border: none;
  background-color: #00ff55d2;
  padding: 0.3em 1.2em;
  border-radius: 30px;
  font-size: 1.2rem;
  color: #000000;
  background-size: 100% 100%;
  font-family: 'Prompt', sans-serif;
}

.buttoncalculate-modal1:hover {
  background-color: #00ff5593;
}

.buttoncalculate-modal2 {
  margin-left: 10px;
  cursor: pointer;
  outline: none;
  border: none;
  background-color: #ff0000b6;
  padding: 0.3em 1.2em;
  border-radius: 30px;
  font-size: 1.2rem;
  color: #000000;
  background-size: 100% 100%;
  font-family: 'Prompt', sans-serif;
}

.buttoncalculate-modal2:hover {
  background-color: #ff000093;
}

/**************************************************/

.input-input1-calculatepointshopp {
  margin-top: 40px;
  margin-left: 10px;
  margin-right: 10px;
  border-radius: 10px;
  font-family: 'Prompt', sans-serif;
}

.input-input2-calculatepointshopp {
  margin-top: 10px;
  margin-left: 10px;
  margin-right: 10px;
  border-radius: 10px;
  border-color: black;
  font-family: 'Prompt', sans-serif;
}

.button-input-calculatepointshop {
  display: inline-block;
  padding: 0.5em 1.7em;
  margin: 0 0.5em 1em 0.5em;
  border: 0.16em solid rgba(255, 255, 255, 0);
  background-color: #00ff5593;
  border-radius: 2em;
  box-sizing: border-box;
  text-decoration: none;
  font-family: 'Prompt', sans-serif;
  font-weight: 300;
  color: #000000;
  text-shadow: 0 0.04em 0.04em rgba(0, 0, 0, 0.35);
  text-align: center;
  transition: all 0.2s;
  margin-top: 10px;
}

.button-input-calculatepointshop:hover {
  border-color: rgb(119, 197, 98);
}

.div-total-price {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.h3-total-price {
  border-top: 2px solid rgb(0, 0, 0);
  padding: 5px 50px;
  margin-top: 20px;
  margin-bottom: 20px;
  color: rgb(0, 0, 0);
  font-family: 'Prompt', sans-serif;
}

.button-total-price {
  display: inline-block;
  padding: 0.5em 1.7em;
  margin: 0 0.1em 0.1em 0;
  border: 0.16em solid rgba(255, 255, 255, 0);
  background-color: rgb(80, 10, 172);
  border-radius: 2em;
  box-sizing: border-box;
  text-decoration: none;
  font-family: 'Prompt', sans-serif;
  font-weight: 300;
  color: #ffffff;
  text-shadow: 0 0.04em 0.04em rgba(0, 0, 0, 0.35);
  text-align: center;
  transition: all 0.2s;
}

.button-total-price:hover {
  border-color: rgb(66, 6, 78);
}

@media (max-width: 414px) {
  .button-delete-item {
    border: none;
    padding: 0.1em 0.1em;
    text-align: center;
    text-decoration: none;
    display: inline-block;
    font-size: 13px;
    border: 2px solid rgb(59, 59, 59);
  }
  .th-table {
    text-align: center;
    vertical-align: middle;
    font-size: 13px;
    font-family: 'Prompt', sans-serif;
  }
  .td-table {
    text-align: center;
    vertical-align: middle;
    font-size: 13px;
  }
}