@import url('https://fonts.googleapis.com/css2?family=Prompt:wght@500&display=swap');



.topuppoint-up {
  width: 300px;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  color: rgb(0, 0, 0);
}

.h1topupoint {
  float: left;
  font-size: 40px;
  border-bottom: 3px solid #929292;
  margin-bottom: 40px;
  padding: 13px 0;
  font-family: 'Prompt', sans-serif;
}

.textbox-topupoint {
  width: 100%;
  overflow: hidden;
  font-size: 20px;
  padding: 8px 0;
  margin: 8px 0;
}

.labeltopupoint{
  font-family: 'Prompt', sans-serif;
}

.input-topupoint {
  font-family: 'Prompt', sans-serif;
  font-size: 16px;
  float: left;
  margin: 10px 10px;
  border-radius: 10px;
  
}

.div-button-topupoint{
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.button-topupoint {
  width: 160px;
  font-size: 16px;
  font-weight: 600;
  color: #fff;
  cursor: pointer;
  margin: 10px 90px;
  height: 45px;
  text-align: center;
  border: none;
  background-size: 300% 100%;
  border-radius: 50px;
  moz-transition: all .4s ease-in-out;
  -o-transition: all .4s ease-in-out;
  -webkit-transition: all .4s ease-in-out;
  transition: all .4s ease-in-out;
  background-image: linear-gradient( to right, #f700ff, #a304ff, #4b00fa, #1100fd);
  box-shadow: 0 4px 15px 0 rgba(174, 0, 255, 0.644);
  font-family: 'Prompt', sans-serif;
}

.button-topupoint:hover {
  background-position: 100% 0;
  moz-transition: all .4s ease-in-out;
  -o-transition: all .4s ease-in-out;
  -webkit-transition: all .4s ease-in-out;
  transition: all .4s ease-in-out;
}

.button-topupoint:focus {
  outline: none;
}