/*แต่งใหม่นิดหน่อย*/

.fa-comments-dollar {
  color: rgb(255, 255, 255);
}

.card-details {
  width: 320px;
  height: 400px;
  position: relative;
  margin: 6% auto;
  background: rgba(3, 3, 3, 0.658);
  box-shadow: 0 6px 20px rgba(3, 3, 3, 0.658);
  padding: 5px;
  overflow: hidden;
  border-radius: 50px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.card-holder-n {
  margin-top: 5px;
  color: rgb(209, 209, 209);
  font-family: 'Prompt', sans-serif;
}

.title-tranfermember {
  color: rgb(209, 209, 209);
  border-bottom: 2px solid rgb(3, 3, 3);
  padding: 5px 10px;
  margin-bottom: 20px;
  font-family: 'Prompt', sans-serif;
}

.form-control {
  width: 100%;
  padding: 10px 0;
  margin: 10px 0px;
}

.form-group-btn-block-m {
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.p-form-group-btn{
  font-family: 'Prompt', sans-serif;
}

.btn-block-m {
  display: inline-block;
  padding: 0.5em 1.7em;
  margin: 0 0.1em 0.1em 0;
  border: 0.16em solid rgba(255, 255, 255, 0);
  background-color: rgb(56, 135, 255);
  border-radius: 2em;
  box-sizing: border-box;
  text-decoration: none;
  font-family: 'Prompt', sans-serif;
  font-weight: 300;
  color: #ffffff;
  text-shadow: 0 0.04em 0.04em rgba(0, 0, 0, 0.35);
  text-align: center;
  transition: all 0.2s;
}

.btn-block-m:hover {
  border-color: rgb(255, 255, 255);
}